import React, { useEffect } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import logo from "../assets/images/logo.png";
import ColorToggle from "../components/ColorToggle";
import { useTranslation } from "react-i18next";
import i18n from "../i18n";
import { Navbar, Nav, Container, Button } from "react-bootstrap";

export const HeaderForgot = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { t } = useTranslation();
  const storedUsername = localStorage.getItem("userLogin");
  const userLogin = JSON.parse(storedUsername);
  useEffect(() => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = JSON.parse(storedUsername);
    if (userLogin !== null) {
      //navigate("/dashboard");
    }
  }, []);

  useEffect(() => {
    const savedLanguage = localStorage.getItem("language") || "en";
    if (i18n.language !== savedLanguage) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n]);

  const changeLanguage = (lang) => {
    i18n.changeLanguage(lang);
    localStorage.setItem("language", lang);
  };

  return (
    <Navbar expand="md" className="py-3 login_header" collapseOnSelect>
      <Container>
        <Navbar.Brand as={Link} to="/">
          <img
            src={logo}
            alt="Logo"
            className="object-fit-contain"
            width="200"
            height="32"
          />
        </Navbar.Brand>
        <ColorToggle />
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ms-auto align-items-md-center gap-3 py-md-0 py-4">
            {userLogin !== null ? (
              <Nav.Link
                as={Link}
                to="/logout"
                className={`topbtn ${
                  location.pathname === "/logout"
                    ? "loginbtn active"
                    : "loginbtn"
                }`}
              >
                {t("Logout")}
              </Nav.Link>
            ) : (
              <>
                <Nav.Link
                  as={Link}
                  to="/login"
                  className={`topbtn ${
                    location.pathname === "/login"
                      ? "loginbtn active"
                      : "loginbtn"
                  }`}
                >
                  {t("LOGIN")}
                </Nav.Link>
                <Nav.Link
                  as={Link}
                  to="/register"
                  className={`topbtn ${
                    location.pathname === "/register"
                      ? "registerbtn active"
                      : "registerbtn"
                  }`}
                >
                  {t("Register")}
                </Nav.Link>
              </>
            )}

            <div className="d-flex align-items-center gap-1 lang_chnage">
              <Button
                variant="light"
                className={`p-1 ${i18n.language === "en" ? "active" : ""}`}
                onClick={() => changeLanguage("en")}
              >
                EN
              </Button>
              <span>-</span>
              <Button
                variant="light"
                className={`p-1 ${i18n.language === "no" ? "active" : ""}`}
                onClick={() => changeLanguage("no")}
              >
                NO
              </Button>
            </div>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};
